import { useMotionValue } from "framer-motion";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import clamp from "lodash/clamp";
import React, {
  useLayoutEffect,
  useRef,
  createContext,
  useContext,
} from "react";

const DEFAULT_OPTIONS = {
  end: "+=100%",
  pin: true,
  scrub: true,
  start: "top top",
};

const ScrollTriggerContext = createContext(null);
const useScrollTrigger = () => useContext(ScrollTriggerContext);

const GSAPScrollTriggerProvider = ({
  children,
  debug = false,
  options = {},
}) => {
  const refScrollTrigger = useRef(null);

  const refTimeline = useRef();

  const scrollYProgress = useMotionValue(0);

  useLayoutEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
    ScrollTrigger.config({
      autoRefreshEvents: "visibilitychange,DOMContentLoaded,load",
    });

    if (refScrollTrigger.current) {
      refTimeline.current = gsap.timeline({
        scrollTrigger: {
          ...DEFAULT_OPTIONS,
          ...options,
          markers: debug,
          trigger: refScrollTrigger.current,
          onUpdate: (instance) => {
            scrollYProgress.set(clamp(instance.progress, 0, 1));
          },
        },
      });
    }

    return () => {
      // Kill and clear the timeline and scrolltrigger instance when updated/unmounted.
      refTimeline.current?.scrollTrigger?.kill();
      refTimeline.current?.kill();
      refTimeline.current?.clear();
    };
  }, [debug, options, scrollYProgress]);

  return (
    <div ref={refScrollTrigger}>
      <ScrollTriggerContext.Provider value={scrollYProgress}>
        {children}
      </ScrollTriggerContext.Provider>
    </div>
  );
};

export { GSAPScrollTriggerProvider, useScrollTrigger };
