import { motion, useTransform } from "framer-motion";
import React from "react";
import classes from "./Screen.module.css";
import History1 from "./History1/History1";
import History2 from "./History2/History2";
import History3 from "./History3/History3";
import NewHorizon from "./NewHorizon/NewHorizon";
import { useScrollTrigger } from "../ScrollTriggerProvider/GSAPScrollTriggerProvider";

const Screen = ({ showProgress = false }) => {
  const scrollYProgress = useScrollTrigger();
  // Fade title in halfway and out at the end.
  // EDUCATION BLOCK ------------------------
  const circle1Opacity = useTransform(
    scrollYProgress,
    [0.05, 0.06],
    [0, 1]
  );
  // EDUCATION BLOCK ENDS -------------------
  // FIRST LANDING BLOCK --------------------
  const circle2Opacity = useTransform(
    scrollYProgress,
    [0.285, 0.295],
    [0, 1]
  );
  // FIRST LANDING BLOCK ENDS ---------------
  // DIVERSION BLOCK ------------------------
  const circle3Opacity = useTransform(
    scrollYProgress,
    [0.515, 0.525],
    [0, 1]
  );
  // DIVERSION BLOCK ENDS -------------------
  // NEW HORIZON BLOCK ----------------------
  const circle4Opacity = useTransform(
    scrollYProgress,
    [0, 0.72, 0.73],
    [0, 0, 1]
  );
  // NEW HORIZON BLOCK ENDS -----------------
  // Animate progress bar to completion.
  const bgProgress = useTransform(
    scrollYProgress,
    [0, 1],
    ["100% 0%", "100% 100%"]
  );

  return (
    <motion.div
      className={classes.Screen}
    >
      <motion.div
        className={classes.Circle1}
        style={{ opacity: circle1Opacity }}
      ></motion.div>
      <motion.div
        className={classes.Circle2}
        style={{ opacity: circle2Opacity }}
      ></motion.div>
      <motion.div
        className={classes.Circle3}
        style={{ opacity: circle3Opacity }}
      ></motion.div>
      <motion.div
        className={classes.Circle4}
        style={{ opacity: circle4Opacity }}
      ></motion.div>
      <History1 />
      <History2 />
      <History3 />
      <NewHorizon />
      {showProgress && (
        <div className={classes.Progress}>
          <motion.div
            className={classes.ProgressInner}
            style={{ backgroundSize: bgProgress }}
          />
        </div>
      )}
    </motion.div>
  );
};

export default Screen;
