import React from "react";
import classes from "./HistoryTag.module.css";
import { motion, useAnimation } from "framer-motion";
import Signal from "../../../assets/Signal.svg";

export const HistoryTag = (props) => {
  const tagImgControls = useAnimation();
  const signalImgControls = useAnimation();
  const popImgControls = useAnimation();
  const popImgControls2 = useAnimation();
  const popImgControls3 = useAnimation();
  const popImgControls4 = useAnimation();
  const popImgControls5 = useAnimation();
  const tagImgVariants = {
    hover: {
      bottom: ["-20%", "-15%"],
      left: ["-20%", "-15%"],
      transition: { duration: 0.3 },
    },
    out: {
      bottom: ["-15%", "-20%"],
      left: ["-15%", "-20%"],
      transition: { duration: 0.3 },
    },
  };
  const signalImgVariants = {
    blinking: {
      opacity: [1, 0, 1, 0, 1],
      transition: { duration: 1.5 },
    },
    blinkingInf: {
      opacity: [0, 1, 0],
      transition: { repeat: Infinity, duration: 1, delay: 1.5 },
    },
    out: {
      opacity: 0,
      transition: { duration: 0.3 },
    },
  };
  const popImgVariants = {
    hover: {
      bottom: "-5%",
      opacity: 1,
      transition: { duration: 0.3 },
    },
    out: {
      bottom: "-50%",
      opacity: 0,
      transition: { duration: 0.3 },
    },
  };
  const popImgVariants2 = {
    hover: {
      bottom: "-5%",
      opacity: 1,
      transition: { duration: 0.3, delay: 0.3 },
    },
    out: {
      bottom: "-50%",
      opacity: 0,
      transition: { duration: 0.3 },
    },
  };
  const popImgVariants3 = {
    hover: {
      bottom: "-5%",
      opacity: 1,
      transition: { duration: 0.3, delay: 0.6 },
    },
    out: {
      bottom: "-50%",
      opacity: 0,
      transition: { duration: 0.3 },
    },
  };
  const popImgVariants4 = {
    hover: {
      bottom: "-5%",
      opacity: 1,
      transition: { duration: 0.3, delay: 0.9 },
    },
    out: {
      bottom: "-50%",
      opacity: 0,
      transition: { duration: 0.3 },
    },
  };
  const popImgVariants5 = {
    hover: {
      bottom: "-5%",
      opacity: 1,
      transition: { duration: 0.3, delay: 1.2 },
    },
    out: {
      bottom: "-50%",
      opacity: 0,
      transition: { duration: 0.3 },
    },
  };
  const tagOnEnter = () => {
    tagImgControls.start("hover");
    props.signal && signalImgControls.start("blinking");
    if (props.imgPop) {
      popImgControls.start("hover");
      popImgControls2.start("hover");
      popImgControls3.start("hover");
      popImgControls4.start("hover");
      popImgControls5.start("hover");
      signalImgControls.start("blinkingInf");
    }
  };
  const tagOnLeave = () => {
    tagImgControls.start("out");
    if (props.imgPop) {
      popImgControls.start("out");
      popImgControls2.start("out");
      popImgControls3.start("out");
      popImgControls4.start("out");
      popImgControls5.start("out");
      signalImgControls.start("out");
    }
  };
  return (
    <div
      onMouseEnter={tagOnEnter}
      onMouseLeave={tagOnLeave}
      onClick={props.clicked}
      className={classes.HistoryTagWrapper}
    >
      <div className={classes.Background}></div>
      <span className={classes.Title}>{props.title}</span>
      {props.imgPop ? (
        <React.Fragment>
          <motion.img
            animate={popImgControls}
            variants={popImgVariants}
            initial={{ bottom: "-50%", left: "-5%", width: "25%", opacity: 0 }}
            className={classes.TagImg}
            src={props.tagImg}
            alt="tag-img"
          />
          <motion.img
            animate={popImgControls2}
            variants={popImgVariants2}
            initial={{ bottom: "-50%", left: "15%", width: "30%", opacity: 0 }}
            className={classes.TagImg}
            src={props.tagImg}
            alt="tag-img"
          />
          <motion.img
            animate={popImgControls3}
            variants={popImgVariants3}
            initial={{ bottom: "-50%", left: "40%", width: "28%", opacity: 0 }}
            className={classes.TagImg}
            src={props.tagImg}
            alt="tag-img"
          />
          <motion.img
            animate={popImgControls4}
            variants={popImgVariants4}
            initial={{ bottom: "-50%", left: "63%", width: "20%", opacity: 0 }}
            className={classes.TagImg}
            src={props.tagImg}
            alt="tag-img"
          />
          <motion.img
            animate={popImgControls5}
            variants={popImgVariants5}
            initial={{ bottom: "-50%", left: "77%", width: "34%", opacity: 0 }}
            className={classes.TagImg}
            src={props.tagImg}
            alt="tag-img"
          />
          <motion.img
            animate={signalImgControls}
            variants={signalImgVariants}
            initial={{
              position: "absolute",
              top: "40%",
              left: "50%",
              transform: "translate(-50%, -50%)",
            }}
            className={classes.Signal}
            src={Signal}
            alt="signal"
          />
        </React.Fragment>
      ) : (
        props.tagImg && (
          <motion.img
            animate={tagImgControls}
            variants={tagImgVariants}
            initial={{ bottom: "-20%", left: "-20%" }}
            className={classes.TagImg}
            src={props.tagImg}
            alt="tag-img"
          />
        )
      )}
      {props.signal && (
        <motion.img
          animate={signalImgControls}
          variants={signalImgVariants}
          initial={{
            bottom: "35%",
            left: "calc(10% + 20px)",
            transform: "rotate(45deg)",
          }}
          className={classes.Signal}
          src={Signal}
          alt="signal"
        />
      )}
      {props.orderNumber && (
        <span className={classes.OrderNumber}>{props.orderNumber}</span>
      )}
    </div>
  );
};
