import React, { useEffect } from "react";
import classes from "./ModalOne.module.css";
import { CloseRounded } from "@mui/icons-material";

export const ModalOne = React.forwardRef((props, ref) => {
  useEffect(() => {
    const close = (e) => {
      if (e.key === 'Escape') {
        props.close();
      }
    };
    window.addEventListener("keydown", close);
    return () => window.removeEventListener("keydown", close);
  }, [props]);
  return (
    <div
      {...props}
      ref={ref}
      className={classes.ModalOne}
      style={{
        backgroundColor: props.bgcolor ? props.bgcolor : "white",
        color: props.color ? props.color : "black",
      }}
    >
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          width: "100%",
          height: "10%",
          justifyContent: "flex-end",
        }}
      >
        <div style={{ display: "flex", marginTop: "2%", marginRight: "2%" }}>
          <b style={{ userSelect: "none" }}>{props.label && props.label}</b>
          <CloseRounded
            onClick={props.close}
            sx={{ marginLeft: "15px", cursor: "pointer" }}
          />
        </div>
      </div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          width: "100%",
          height: "95%",
        }}
      >
        {props.children}
      </div>
    </div>
  );
});
