import { motion, useTransform, useAnimation } from "framer-motion";
import React, { useContext, useState, memo } from "react";
import classes from "./History3.module.css";
import LogoVinhomes from "../../../assets/Vinhomes.png";
import { SwaggerTag } from "./SwaggerTag/SwaggerTag";
import { AQI } from "./AQI/AQI";
import axios from "axios";
import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import { animateScroll as scroll } from "react-scroll/modules";
import useWindowDimensions from "../../../utils/useWindowDimensions";
import { CommonContext } from "../../../context/CommonContext";
import { HistoryTag } from "../../UI/HistoryTag/HistoryTag";
import { useScrollTrigger } from "../../ScrollTriggerProvider/GSAPScrollTriggerProvider";
import { Backdrop, Modal, Fade } from "@mui/material";
import { ModalOne } from "../../UI/ModalOne/ModalOne";
import Building from "../../../assets/Building.svg";
import Project5_1 from "../../../assets/Project5_1.jpg";
import Project5_2 from "../../../assets/Project5_2.jpg";
import useImagePreloader from "../../../utils/useImagePreloader";
import Loader from "../../UI/Loader/Loader";

const History3 = () => {
  const preloadSrcList = [Project5_1, Project5_2];
  const { imagesPreloaded } = useImagePreloader(preloadSrcList);
  const scrollYProgress = useScrollTrigger();
  const [aqi, setAqi] = useState();
  const [parkingSlot, setParkingSlot] = useState("---");
  const [commonState, dispatch] = useContext(CommonContext);
  const { height } = useWindowDimensions();
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
    document.documentElement.style.overflowY = "hidden";
  };
  const handleClose = () => {
    setOpen(false);
    document.documentElement.style.overflowY = "scroll";
  };

  let progressBarTextColor = "#ccc";
  if (parkingSlot !== "---") {
    progressBarTextColor = "#FAB042";
  }

  const getAqi = () => {
    axios
      .get(
        "https://api.waqi.info/feed/hanoi/?token=7779710e62084202bf33d7e750121ae64682fa28"
      )
      .then((response) => {
        setAqi(response.data.data.aqi);
      })
      .catch((error) => console.log(error));
  };

  const getRandomNumber = () => {
    axios
      .get(
        "http://www.randomnumberapi.com/api/v1.0/random?min=0&max=180&count=1"
      )
      .then((response) => {
        setParkingSlot(response.data[0]);
      })
      .catch((error) => console.log(error));
  };

  const vinhomesOpacity = useTransform(
    scrollYProgress,
    // [0.05, 0.11, 0.29, 0.3],
    // [0.355, 0.415, 0.53, 0.54],
    [0.525, 0.555, 0.715, 0.72],
    [0, 1, 1, 0]
  );

  const smallLogoOpacity = useTransform(
    scrollYProgress,
    // [0.3, 0.31, 1],
    // [0.54, 0.55, 1],
    [0.715, 0.725],
    [0, 1]
  );

  const smallLogoDisplay = useTransform(
    scrollYProgress,
    // [0, 0.539, 0.54],
    [0, 0.714, 0.715],
    ["none", "none", "flex"]
  );

  const vinhomesDisplay = useTransform(
    scrollYProgress,
    // [0, 0.05, 0.11, 0.3, 0.301],
    // [0, 0.355, 0.415, 0.54, 0.541],
    [0, 0.525, 0.555, 0.715, 0.716],
    ["none", "none", "flex", "flex", "none"]
  );

  const smallLogoVariant = {
    tap: { scale: 0.98 },
    hover: { scale: 1.05, height: "200%" },
    hoverTag: { height: "100%" },
    hoverPrg: { height: "200%" },
    hoverLogo: { scale: 1.02 },
  };
  const horImgControls = useAnimation();
  const horImgControls2 = useAnimation();
  const horImgVariant = {
    hover: { scale: 1.05, height: "400%", width: "50%" },
    out: { scale: 1, height: "45%", width: "100%" },
  };
  const horImgVariant2 = {
    hover: { scale: 1.05, height: "400%", width: "50%" },
    out: { scale: 1, height: "45%", width: "100%" },
  };
  const horDescControls = useAnimation();
  const horDescVariant = {
    hover: { opacity: 1, transition: { duration: 0.1 } },
    out: { opacity: 0, transition: { duration: 0.1 } },
  };
  const horDescControls2 = useAnimation();
  const horDescVariant2 = {
    hover: { opacity: 1, transition: { duration: 0.1 } },
    out: { opacity: 0, transition: { duration: 0.1 } },
  };
  const modal = (
    <Modal
      aria-labelledby="transition-modal-title"
      aria-describedby="transition-modal-description"
      open={open}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      disableAutoFocus={true}
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Fade in={open}>
        <ModalOne label="vinhomes" close={handleClose}>
          <div
            style={{
              position: "relative",
              height: "100%",
              width: "50%",
              display: "flex",
              flexDirection: "column",
              justifyContent: "flex-start",
              marginLeft: "2%",
              marginRight: "2%",
            }}
          >
            <motion.div
              variants={smallLogoVariant}
              whileHover="hoverLogo"
              initial={{ height: "60%" }}
              style={{
                height: "60%",
                overflow: "auto",
                display: "flex",
                flexDirection: "column",
                margin: "5%",
              }}
            >
              <h3 style={{ margin: "auto" }}>vinhomes smartcity solution</h3>
              <hr
                style={{
                  width: "80%",
                  backgroundColor: "rgb(89, 39, 239)",
                  height: "0.5px",
                }}
              ></hr>
              <ul style={{ margin: "auto" }}>
                <li>
                  <b>responsibilities</b>:
                </li>
                <ul>
                  <li>
                    developed an ai camera module using sigmastar soc ssc339g
                  </li>
                  <li>integrated face recognition model on jetson platform</li>
                  <li>
                    developed multiple microservices for vinhomes smartcity hub
                  </li>
                </ul>
                <li>
                  <b>developed products</b>:
                </li>
                <ul>
                  <li>
                    powering all smart parking features in
                    <a
                      target="_blank"
                      rel="noopener noreferrer"
                      href="https://apps.apple.com/vn/app/vinhomes-resident/id1558477403"
                      style={{
                        color: "rgb(89, 39, 239)",
                        textDecoration: "none",
                        whiteSpace: "pre",
                        fontWeight: "bold",
                      }}
                    >
                      {` vinhomes resident mobile app`}
                    </a>
                  </li>
                  <li>
                    <a
                      target="_blank"
                      rel="noopener noreferrer"
                      href="https://www.vsmart.net/en/smart-system"
                      style={{
                        color: "rgb(89, 39, 239)",
                        textDecoration: "none",
                        fontWeight: "bold",
                      }}
                    >
                      state-of-the-art smartcity solution
                    </a>
                    <ul>
                      <li>smart device monitoring module</li>
                      <li>
                        ai events monitoring from thousands of cameras installed
                        around vinhomes' complex
                      </li>
                      <li>real-time parking ramps/basements monitoring</li>
                      <li>private erp</li>
                      <li>and many more features...</li>
                    </ul>
                  </li>
                </ul>
                <li>
                  <b>acquired skills</b>:
                </li>
                <ul>
                  <li>java, spring boot, spring security</li>
                  <li>elasticsearch, cassandra, postgresql, kafka</li>
                  <li>
                    haproxy, nginx, kubernetes, docker, helm, jenkins, nifi
                  </li>
                  <li>reactjs</li>
                </ul>
              </ul>
            </motion.div>
            <motion.div
              variants={smallLogoVariant}
              whileHover="hoverTag"
              initial={{ height: "30%" }}
              className={classes.ImgDiv}
              style={{
                position: "relative",
                objectFit: "cover",
                borderRadius: "20px",
                overflow: "hidden",
                boxShadow: "0px 5px 15px 0.5px #edeffc",
                margin: "5%",
                overflowY: "scroll",
              }}
            >
              <div className={classes.Tag1}>
                <SwaggerTag
                  clicked={getAqi}
                  get
                  path="/aqi"
                  description="get hanoi air quality index"
                />
              </div>
              <div className={classes.Tag1}>
                <SwaggerTag
                  get
                  clicked={getRandomNumber}
                  path="/parkings"
                  description="get total empties/occupied parking slots of all parkings"
                />
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "center",
                }}
              >
                <div className={classes.AQI}>
                  <AQI aqiValue={aqi} />
                </div>
                <div className={classes.CircularPB}>
                  <CircularProgressbar
                    value={parkingSlot}
                    maxValue="180"
                    text={`${parkingSlot}/180`}
                    styles={buildStyles({
                      textColor: progressBarTextColor,
                      pathColor: "#FAB042",
                      trailColor: "#ccc",
                    })}
                  />
                </div>
              </div>
            </motion.div>
          </div>
          <div
            style={{
              position: "relative",
              height: "100%",
              width: "50%",
              display: "flex",
              flexDirection: "column",
              justifyContent: "flex-start",
              marginLeft: "2%",
              marginRight: "2%",
              alignItems: "center",
            }}
          >
            <div
              onMouseEnter={() => {
                horImgControls.start("hover");
                horDescControls.start("hover");
              }}
              onMouseLeave={() => {
                horImgControls.start("out");
                horDescControls.start("out");
              }}
              style={{
                position: "absolute",
                height: "45%",
                width: "100%",
                margin: "5%",
                zIndex: 10,
              }}
            ></div>
            <div
              onMouseEnter={() => {
                horImgControls2.start("hover");
                horDescControls2.start("hover");
              }}
              onMouseLeave={() => {
                horImgControls2.start("out");
                horDescControls2.start("out");
              }}
              style={{
                position: "absolute",
                height: "45%",
                width: "100%",
                margin: "5%",
                top: "50%",
                zIndex: 10,
              }}
            ></div>
            <motion.div
              variants={horImgVariant}
              animate={horImgControls}
              className={classes.ImgDiv}
              initial={{ height: "45%", width: "100%" }}
              style={{
                position: "relative",
                objectFit: "cover",
                borderRadius: "20px",
                overflow: "hidden",
                boxShadow: "0px 5px 15px 0.5px #edeffc",
                margin: "5%",
              }}
            >
              {imagesPreloaded ? (
                <img
                  style={{
                    height: "100%",
                    width: "100%",
                    objectFit: "cover",
                  }}
                  src={Project5_1}
                  alt="proj_5_1"
                />
              ) : (
                <Loader />
              )}
              <motion.div
                variants={horDescVariant}
                animate={horDescControls}
                className={classes.ImgDesc}
              >
                parking status monitor
              </motion.div>
            </motion.div>
            <motion.div
              variants={horImgVariant2}
              animate={horImgControls2}
              initial={{ height: "45%", width: "100%" }}
              className={classes.ImgDiv}
              style={{
                position: "relative",
                objectFit: "cover",
                borderRadius: "20px",
                overflow: "hidden",
                boxShadow: "0px 5px 15px 0.5px #edeffc",
                margin: "5%",
              }}
            >
              {imagesPreloaded ? (
                <img
                  style={{
                    height: "100%",
                    width: "100%",
                    objectFit: "cover",
                  }}
                  src={Project5_2}
                  alt="proj_5_2"
                />
              ) : (
                <Loader />
              )}
              <motion.div
                variants={horDescVariant2}
                animate={horDescControls2}
                className={classes.ImgDesc}
              >
                pay parking fee remotely
              </motion.div>
            </motion.div>
          </div>
        </ModalOne>
      </Fade>
    </Modal>
  );
  return (
    <React.Fragment>
      <div
        style={{
          top: "35%",
          left: "90px",
          width: "80vw",
          display: "flex",
          flexDirection: "column",
          position: "absolute",
        }}
      >
        <motion.section
          id="history-and-bullets-3"
          className={classes.History3}
          style={{ opacity: vinhomesOpacity, display: vinhomesDisplay }}
        >
          <img alt="utc-logo" src={LogoVinhomes} />
          <div style={{ marginLeft: "25px" }}>
            <p style={{ marginTop: "8px", marginBottom: "8px", color: "#888" }}>
              <b>
                [ diversion  ] <br /> software engineer (apr 2021 - now)
              </b>
            </p>
            <h2 style={{ marginTop: "8px", marginBottom: "8px" }}>
              vinhomes, hanoi, vietnam
            </h2>
          </div>
        </motion.section>
        <motion.div
          style={{
            opacity: vinhomesOpacity,
            display: vinhomesDisplay,
            width: "100%",
            overflow: "auto",
          }}
        >
          <HistoryTag
            title="vinhomes smartcity solution"
            clicked={handleOpen}
            tagImg={Building}
            imgPop
          />
        </motion.div>
      </div>
      {modal}
      <motion.div
        variants={smallLogoVariant}
        whileTap="tap"
        whileHover="hoverLogo"
        style={{
          opacity: smallLogoOpacity,
          cursor: "pointer",
          display: smallLogoDisplay,
        }}
        className={classes.SmallHistoryDiv}
        onClick={() =>
          scroll.scrollTo(height * commonState.heightMul * 0.555 + height * 1.1)
        }
        onMouseEnter={() => {
          dispatch({
            type: "SET_HOVERING",
            payload: {
              hovering: "vhm",
            },
          });
        }}
        onMouseLeave={() => {
          dispatch({
            type: "SET_HOVERING",
            payload: {
              hovering: "default",
            },
          });
        }}
      >
        <motion.img className={classes.SmallLogo} src={LogoVinhomes} />
        <h2 style={{ paddingLeft: "15px", width: "50%", margin: 0 }}>
          vinhomes
        </h2>
      </motion.div>
    </React.Fragment>
  );
};

export default memo(History3);
