import React, { useState, useContext, memo } from "react";
import classes from "./NewHorizon.module.css";
import { motion, useTransform } from "framer-motion";
import { TextField } from "@mui/material";
import ReactImageUploading from "react-images-uploading";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import { useScrollTrigger } from "../../ScrollTriggerProvider/GSAPScrollTriggerProvider";
import EmailIcon from "@mui/icons-material/Email";
import { ColorExtractor } from "react-color-extractor";
import { CommonContext } from "../../../context/CommonContext";

const NewHorizon = () => {
  const [, dispatch] = useContext(CommonContext);
  const [companyName, setCompanyName] = useState("");
  const [, setColors] = useState([]);
  const scrollYProgress = useScrollTrigger();
  const [images, setImages] = useState([]);
  const onChange = (imageList) => {
    setImages(imageList);
  };
  const maxNumber = 1;
  const nhDisplay = useTransform(
    scrollYProgress,
    // [0, 0.05, 0.11, 0.3, 0.301],
    // [0, 0.355, 0.415, 0.54, 0.541],
    // [0, 0.585, 0.645, 0.77, 0.771],
    [0, 0.72, 0.721],
    ["none", "none", "flex"]
  );
  const nhOpacity = useTransform(
    scrollYProgress,
    // [0.05, 0.11, 0.29, 0.3],
    // [0.355, 0.415, 0.53, 0.54],
    // [0.585, 0.645, 0.76, 0.77],
    [0.72, 0.76],
    [0, 1]
  );
  const nhTitleOpacity = useTransform(scrollYProgress, [0.76, 0.8], [0, 1]);
  const nhBulletPoint = useTransform(scrollYProgress, [0.8, 0.84], [0, 1]);

  const style = {
    "& label.Mui-focused": {
      color: "rgb(89, 39, 239)",
    },
    // focused color for input with variant='standard'
    "& .MuiInput-underline:after": {
      borderBottomColor: "rgb(89, 39, 239)",
    },
    // focused color for input with variant='filled'
    "& .MuiFilledInput-underline:after": {
      borderBottomColor: "rgb(89, 39, 239)",
    },
    // focused color for input with variant='outlined'
    "& .MuiOutlinedInput-root": {
      "&.Mui-focused fieldset": {
        borderColor: "rgb(89, 39, 239)",
      },
    },
  };

  return (
    <React.Fragment>
      <div
        style={{
          top: "65%",
          left: "90px",
          width: "80vw",
          display: "flex",
          flexDirection: "column",
          position: "absolute",
        }}
      >
        <motion.section
          id="history-and-bullets-3"
          className={classes.NewHorizon}
          style={{ opacity: nhOpacity, display: nhDisplay }}
        >
          {/* <img alt="utc-logo" src={LogoVinhomes} /> */}
          <ReactImageUploading
            multiple
            maxNumber={maxNumber}
            value={images}
            onChange={onChange}
            dataURLKey="data_url"
          >
            {({
              imageList,
              onImageUpload,
              onImageUpdate,
              isDragging,
              dragProps,
            }) => (
              // write your building UI
              <div
                className={classes.BeautifulLogo}
                style={{
                  width: "150px",
                  height: "150px",
                  display: "flex",
                  alignItems: "center",
                  backgroundColor: "white",
                  borderRadius: "10px",
                  boxShadow: "0px 5px 15px 0.5px #edeffc",
                }}
              >
                {imageList.length === 0 ? (
                  <p
                    style={isDragging ? { color: "rgb(89, 39, 239)" } : null}
                    onClick={onImageUpload}
                    {...dragProps}
                  >
                    <strong>
                      so upload your company logo here (drag/click)
                    </strong>
                  </p>
                ) : (
                  imageList.map((image, index) => (
                    <div key={index} className="image-item">
                      <ColorExtractor
                        rgb
                        getColors={(colors) => {
                          setColors(colors);
                          const transformedColors = colors.map(
                            (color, index) => {
                              let alpha = 0;
                              if (index === 0) {
                                alpha = 0.55;
                              } else if (index === 1) {
                                alpha = 0.5;
                              } else if (index === 2) {
                                alpha = 0.4;
                              }
                              return (
                                "rgba(" + color.join(",") + "," + alpha + ")"
                              );
                            }
                          );
                          dispatch({
                            type: "SET_CUSTOM_COLORS",
                            payload: {
                              colors: transformedColors,
                            },
                          });
                        }}
                      >
                        <img
                          {...dragProps}
                          onClick={() => onImageUpdate(index)}
                          src={image.data_url}
                          alt=""
                          width="100%"
                          height="100%"
                          style={{ objectFit: "cover" }}
                        />
                      </ColorExtractor>
                    </div>
                  ))
                )}
              </div>
            )}
          </ReactImageUploading>
          <div style={{ marginLeft: "25px" }}>
            <p style={{ marginTop: "8px", marginBottom: "8px", color: "#888" }}>
              <b>[ new horizon  ]</b>
            </p>
            <motion.div
              style={{
                opacity: nhTitleOpacity,
              }}
            >
              <TextField
                sx={style}
                inputProps={{ style: { fontSize: "20px" } }}
                InputLabelProps={{ style: { fontSize: "20px" } }}
                className={classes.InputField}
                label="then your company name here"
                variant="standard"
                onChange={(e) => {
                  setCompanyName(e.target.value);
                }}
              ></TextField>
            </motion.div>
            <motion.ul style={{ opacity: nhBulletPoint }}>
              <motion.li>
                and imagine all the exciting things we will achieve together
                here
              </motion.li>
              <motion.li style={{ color: "rgb(89,39,239)" }}>
                <b>so reach out to me at:</b>
              </motion.li>
            </motion.ul>
          </div>
        </motion.section>
        <motion.div
          style={{
            opacity: nhBulletPoint,
            display: "flex",
            flexDirection: "row",
            marginLeft: "240px",
          }}
        >
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.linkedin.com/in/anh-nguyen-19201a16b"
            className={classes.Icons}
          >
            <LinkedInIcon style={{ fontSize: "100px" }} />
          </a>
          <a
            href={`mailto:anhnguyen97utc@gmail.com?subject=Hello Anh Nguyen! We're from ${companyName}`}
            className={classes.Icons}
            style={{ marginRight: 0 }}
          >
            <EmailIcon style={{ fontSize: "100px" }} />
          </a>
        </motion.div>
      </div>
    </React.Fragment>
  );
};

export default memo(NewHorizon);
