import React, { memo } from "react";
import classes from "./Header.module.css";
import { CircleBackground } from "../CircleBackground/CircleBackground";
import { animateScroll as scroll } from "react-scroll/modules";
import { useMediaQuery } from "react-responsive";
import Typewriter from "typewriter-effect";

const Header = (props) => {
  const isDesktopOrLaptop = useMediaQuery({
    query: "(min-width: 1224px)",
  });
  const isPortrait = useMediaQuery({ orientation: "portrait" });
  let message = `scroll down or click here to scroll to the bottom for contact information`;
  if (!isDesktopOrLaptop && isPortrait) {
    message = `swipe up or tap here to swipe to the bottom for contact information`;
  }
  return (
    <React.Fragment>
      <CircleBackground />
      <div className={classes.Header}>
        <h1>[anh, nguyen trong quy]</h1>
        <div className={classes.Introduction}>
          {`i'm `}
          <Typewriter
            options={{ delay: 75, cursor: '<span style="color: rgb(89, 39, 239)">_</span>', loop: true }}
            onInit={(typewriter) => {
              typewriter
                .pauseFor(500)
                .typeString('<span style="color: rgb(89, 39, 239)">an embedded</span>')
                .pauseFor(1500)
                .deleteAll("natural")
                .typeString('<span style="color: rgb(89, 39, 239)">a backend</span>')
                .pauseFor(1500)
                .deleteAll("natural")
                .typeString('<span style="color: rgb(89, 39, 239)">a frontend</span>')
                .pauseFor(1500)
                .deleteAll("natural")
                .start();
            }}
          />{`developer`}
        </div>
      </div>
      <p
        style={{
          position: "absolute",
          bottom: "2%",
          width: "100%",
          display: "flex",
          justifyContent: "center",
          opacity: 0.5,
          fontSize: "normal",
        }}
        onClick={() => {
          scroll.scrollToBottom();
        }}
      >
        {message}
      </p>
      <div
        style={{
          position: "absolute",
          bottom: "10%",
          display: "flex",
          width: "100%",
          justifyContent: "center",
        }}
      >
        <div className={classes.Mouse}></div>
      </div>
    </React.Fragment>
  );
};

export default memo(Header);