import React from "react";
import classes from "./AQI.module.css";

export const AQI = (props) => {
  let floatAqiValue = '---';
  if (props.aqiValue !== undefined) {
    floatAqiValue = parseFloat(props.aqiValue);
    // console.log(floatAqiValue);
  }
  let backgroundColor = "#ccc";
  let color = "#444";
  if (floatAqiValue) {
    if (floatAqiValue >= 0 && floatAqiValue <= 5) {
      backgroundColor = "#5FABCB";
      color = "#274551";
    } else if (floatAqiValue >= 5.1 && floatAqiValue <= 10) {
      backgroundColor = "#9CD84E";
      color = "#3F5721";
    } else if (floatAqiValue >= 10.1 && floatAqiValue <= 15) {
      backgroundColor = "#FACF38";
      color = "#645318";
    } else if (floatAqiValue >= 15.1 && floatAqiValue <= 25) {
      backgroundColor = "#F99049";
      color = "#643A1E";
    } else if (floatAqiValue >= 25.1 && floatAqiValue <= 35) {
      backgroundColor = "#F65E5F";
      color = "#FFF";
    } else if (floatAqiValue >= 35.1 && floatAqiValue <= 50) {
      backgroundColor = "#A070B6";
      color = "#FFF";
    } else if (floatAqiValue >= 50.1) {
      backgroundColor = "#A06A7B";
      color = "#FFF";
    }
  }
  return (
    <div
      style={{ backgroundColor: backgroundColor, color: color }}
      className={classes.AQIWrapper}
    >
      <span className={classes.AQIValue}>{floatAqiValue}</span>
      <span className={classes.AQILocation}>PM2.5 <br/>Hanoi</span>
    </div>
  );
};
