import React, { useContext } from "react";
import classes from "./CircleBackground.module.css";
import { CommonContext } from "../../context/CommonContext";

export const CircleBackground = () => {
  const [commonContext] = useContext(CommonContext);
  let childSpanColors = null;
  if (commonContext.hovering === "default") {
    if (commonContext.customColors === true) {
      childSpanColors = commonContext.colors;
    } else {
      childSpanColors = [
        "rgba(89, 39, 239, 0.55)",
        "rgba(89, 39, 239, 0.5)",
        "rgba(89, 39, 239, 0.4)",
      ];
    }
  } else if (commonContext.hovering === "utc") {
    childSpanColors = [
      "rgba(34, 15, 86, 0.55)",
      "rgba(251, 203, 6, 0.5)",
      "rgba(253, 249, 183, 0.4)",
    ];
  } else if (commonContext.hovering === "fpt") {
    childSpanColors = [
      "rgba(9, 76, 163, 0.55)",
      "rgba(243, 112, 33, 0.5)",
      "rgba(79, 182, 71, 0.4)",
    ];
  } else if (commonContext.hovering === "vhm") {
    childSpanColors = [
      "rgba(250, 176, 66, 0.55)",
      "rgba(2, 73, 191, 0.5)",
      "rgba(232, 195, 139, 0.4)",
    ];
  }
  return (
    <div className={classes.Background}>
      <span style={{ color: childSpanColors[0] }}></span>
      <span style={{ color: childSpanColors[1] }}></span>
      <span style={{ color: childSpanColors[2] }}></span>
      <span style={{ color: childSpanColors[0] }}></span>
      <span style={{ color: childSpanColors[1] }}></span>
      <span style={{ color: childSpanColors[2] }}></span>
      <span style={{ color: childSpanColors[0] }}></span>
      <span style={{ color: childSpanColors[1] }}></span>
      <span style={{ color: childSpanColors[2] }}></span>
    </div>
  );
};
